<template>
	<view class="plan-box" :style="{paddingTop:paddingTop +'px'}" @click="hideInfo">
		<view class="plan-box-head">
			<nav-bar @back="goBack" title="制定学习计划"></nav-bar>
		</view>
		<view class="plan-box-content">
			<view class="content-list">
				<view class="list-plan-li">
					<view class="li-title">词库</view>
					<view class="li-text">高考高频词汇</view>
				</view>
				<view class="list-plan-li">
					<view class="li-title">单词个数</view>
					<view class="li-text">200</view>
				</view>
				<view class="list-plan-msg">
					<view class="text">每日学习单词数</view>
					<view class="list-msg-img" @click.stop="msgInfo">
						<image src="../../static/image/msg-ico.png" mode="widthFix"></image>
					</view>

					<view class="msg-content" v-if="isMsg">
						<view class="txt">
							你可以根据自己的学习需求，设置每天需要完成的新词学习数量。
						</view>
						<view class="txt">
							我们会根据你设置的新词学习数量，来分配任务数，任务进行天数由学习+复习时长来确定。
						</view>
						<view class="txt">
							预计完成日历取决于每日是否完整完成任务，若当日无法完成，则截止天数往后推移。
						</view>
						<view class="txt">
							每日任务安排可查看学习计划日历。
						</view>
					</view>
				</view>
				<view class="list-plan-li">
					<view class="li-title">每日学习单词数</view>
					<view class="li-text">
						<picker @change="bindPickerChange" :value="index" :range="array">
							<view class="uni-input">{{array[index]}}</view>
						</picker>
					</view>
				</view>
				<!-- <view class="msg-number-box">
					
				</view> -->
				<view class="list-plan-li">
					<view class="li-title">总任务数</view>
					<view class="li-text">50</view>
				</view>

				<view class="list-plan-li">
					<view class="li-title">任务进行天数</view>
					<view class="li-text">20</view>
				</view>
				<view class="list-plan-li">
					<view class="li-title">预计完成日期</view>
					<view class="li-text">2020-12-29</view>
				</view>
				<view class="list-plan-li">
					<view class="li-title">学习计划日历</view>
					<view class="li-ico">
						<image src="../../static/image/calendar-ico.png" mode="widthFix"></image>
					</view>
				</view>
			</view>
		</view>
		<view class="plan-box-footer">
			<nav-btn btnName='开始学习' @clickBtn="startStudy"></nav-btn>
		</view>
	</view>
</template>

<script>
	import navBar from '@/components/nav-bar/nav-bar'
	import navBtn from '@/components/nav-btn/nav-btn.vue'
	export default {
		data() {
			return {
				isMsg: false,
				paddingTop: 0,
				index: 0,
				array: ['1', '2', '3', '4'],
			}
		},
		components: {
			navBar,
			navBtn
		},
		onLoad() {
			uni.getSystemInfo({
				success: (res) => {
					this.paddingTop = res.statusBarHeight
				}
			});
		},
		methods: {
			// 返回
			goBack() {
				uni.navigateBack({})
			},

			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.index = e.target.value
			},

			// 手指长按显示任务内容
			msgInfo() {
				this.isMsg = true
			},
			// 隐藏内容
			hideInfo() {
				this.isMsg = false
			},

			// 开始学习按钮
			startStudy() {
				uni.reLaunch({
					url: "../home/home?isTask=true"
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	.plan-box {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		box-sizing: border-box;

		.plan-box-head {}

		.plan-box-content {
			width: 100%;
			padding: 0 48rpx;
			box-sizing: border-box;
			flex: 1;

			.content-list {
				.list-plan-li {
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding: 30rpx 0;
					font-weight: 500;

					.li-title {
						font-size: 28rpx;
						color: $uni-text-color;
					}

					.li-text {
						font-size: 22rpx;
						color: #939399;
					}

					.li-ico {
						image {
							width: 40rpx;
							height: 40rpx;
						}
					}
				}

				.list-plan-msg {
					position: relative;
					display: flex;
					align-items: center;
					justify-content: center;
					height: 50rpx;

					.text {
						font-size: 28rpx;
						color: $uni-text-color;
					}

					.list-msg-img {
						display: flex;
						align-items: center;
						justify-content: center;
						width: 50rpx;
						height: 50rpx;

						image {

							width: 26rpx;
							height: 26rpx;
						}
					}


					.msg-content {
						position: absolute;
						top: 64rpx;
						width: 480rpx;
						height: 448rpx;
						padding: 40rpx;
						box-sizing: border-box;
						background: rgba(0, 0, 0, 0.7);
						border-radius: 12rpx;

						.txt {
							margin-bottom: 16rpx;
							font-size: 24rpx;
							color: #fff;
							line-height: 36rpx;
						}
					}

					.msg-content::after {
						content: '';
						position: absolute;
						top: -18rpx;
						right: 132rpx;
						width: 0;
						height: 0;
						border-top: 0rpx solid transparent;
						border-right: 10rpx solid transparent;
						border-bottom: 18rpx solid rgba(0, 0, 0, 0.7);
						border-left: 10rpx solid transparent;
					}
				}

				.msg-number-box {
					height: 60rpx;
				}
			}
		}

		.plan-box-footer {
			padding: 0 48rpx 96rpx;
			box-sizing: border-box;
		}
	}
</style>
